import React from "react";
import Layout from "../components/layout";
import { graphql, Link } from "gatsby";
import { css } from "@emotion/core";

export default ({ data }) => {
  return (
        <Layout>

                <h2 css={css`border: solid 2px #000;text-align: center;margin: 20px;padding: 10px;`}>404 </h2>
              {data.allMarkdownRemark.edges.map(({ node }, index) =>
                <div key={index} css={css`padding: 20px; background-color: #ccc;border-bottom: solid 1px #bbb;`}>
            
            Trang này không tồn tại, bạn hãy quay lại trang chủ nhé.
                </div>
              )}
        </Layout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark(
        sort: {order: DESC, fields: [frontmatter___date] }
        limit: 1000
      ) {
      edges {
        node {
          frontmatter {
            title
            date(formatString: "DD/MM/YYYY")
            thumbnail {
              childImageSharp {
                fixed(width: 300) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
          excerpt
          fields {
            slug
          }
        }
      }
    }
  }
`